<template>
    <div :class="classlist" :style="{ 'background-image': `url(${imageUrl})` }">
        <button type="button" @click="block.execute('chose-image', { placement: 'above' })"></button>
    </div>
</template>

<script setup>
const { defineProps, computed, reactive, inject } = require('vue');
const props = defineProps(['block', 'class', 'image']);
const options = inject('options');
const filemanager = inject('filemanager');

const classlist = computed(() => {
    return 'tued-block_editable-background-image ' + props.class;
});

const imageUrl = computed(() => {
    if (props.image.id) {
        return filemanager.generatePreviewImagePath(props.image);
    }

    return 'data:image/png;base64,';
})
</script>

<script>
export default {
    name: 'BackgroundImageEditor'
}
</script>
